<template>
  <div class="list row">
    <div class="col-md-6">
      <h4>Category List</h4>
      <ul class="list-group">
        <li
          class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(c, index) in category"
          :key="index"
          @click="setActiveCategory(c, index)"
        >
          {{ c.value }}
        </li>
      </ul>
    </div>
    <div class="col-md-6" style="margin-bottom: 32px">
      <div v-if="currentCategory._id">
        <h4>Category</h4>
        <div>
          <label><strong>ID:</strong></label> {{ currentCategory._id }}
        </div>
        <div>
          <label><strong>Value:</strong></label> {{ currentCategory.value }}
        </div>
        <div>
          <label><strong>Text:</strong></label>
          {{ currentCategory.text }}
        </div>
        <div>
          <label><strong>Image:</strong></label>
          <div style="display: flex" v-if="currentCategory.image">
            <img class="display-image" :src="currentCategory.image" />
          </div>
          <p v-else>null</p>
        </div>

        <router-link
          :to="'/category/' + currentCategory._id"
          class="badge badge-warning"
          >Edit</router-link
        >
      </div>
      <div v-else>
        <br />
        <p>Please click on a Category...</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Category from "@/types/Category";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "category-list",
  data() {
    return {
      category: [] as Category[],
      currentCategory: {} as Category,
      currentIndex: -1,
      name: "",
    };
  },
  methods: {
    retrieveCategory() {
      DataService.getAllCategory()
        .then((response: ResponseData) => {
          this.category = response.data.category;
          console.log(this.category);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveCategory();
      this.currentCategory = {} as Category;
      this.currentIndex = -1;
    },

    setActiveCategory(category: Category, index = -1) {
      this.currentCategory = category;
      this.currentIndex = index;
    },
  },
  mounted() {
    this.retrieveCategory();
  },
});
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
.display-image {
  height: 100px;
  object-fit: contain;
  margin: 4px;
}
.badge-warning {
  margin-top: 12px;
  border: none;
  border-radius: 6px;
  padding: 16px 24px;
}
</style>
